import * as React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"
import { MDXRenderer } from "gatsby-plugin-mdx"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import Breadcrumbs from "../../components/breadcrumbs"
import { Button } from "../../components/button"

import "../../styles/product.css"
import { PHONE_NUMBER_MAIN } from "../../components/consts"

const ProductPage = ({ data }) => {
  const { body } = data.mdx
  const {
    title,
    size,
    weight,
    mark,
    howMany,
    price,
    image,
    galery,
    description,
    keywords,
  } = data.allMdx.nodes[0].frontmatter
  const [actualImg, setActuaImg] = React.useState(getImage(image))
  const ref = React.useRef(null)
  const onClickGaleryImg = e =>
    (ref.current = e.target.dataset.imageValue) &&
    setActuaImg(getImage(galery.filter(img => img?.id === ref.current)[0]))

  return (
    <Layout>
      <Seo title={title} description={description} keywords={keywords} />
      <div className="products-image"></div>
      <section className="products">
        <div className="container">
          <Breadcrumbs {...data.allMdx.nodes[0].frontmatter} />
          <div className="product-container">
            {galery && (
              <div className="product__galery">
                {galery.map(galeryImage => {
                  const galeryImg = getImage(galeryImage)
                  return (
                    galeryImage !== null && (
                      <div
                        key={galeryImage.id}
                        ref={ref}
                        className={`product__galery-item`}
                      >
                        <GatsbyImage
                          image={galeryImg}
                          alt={title}
                          onClick={onClickGaleryImg}
                          data-image-value={galeryImage.id}
                        />
                      </div>
                    )
                  )
                })}
              </div>
            )}
            <div className="product__image">
              <GatsbyImage image={actualImg} alt={title} />
            </div>
            <div className="product__info">
              <h1>{title}</h1>
              {size && <p className="product__info-params">Размер: {size}</p>}
              {weight && (
                <p className="product__info-params">
                  Вес: {weight.value} <small>{weight.value_type}</small>
                </p>
              )}
              {mark && (
                <p className="product__info-params">Марка прочности: {mark}</p>
              )}
              {howMany && (
                <p className="product__info-params">
                  Кол-во в 1 п.м: {howMany}
                </p>
              )}
              {price.cost && (
                <p className="product__info-price">
                  Серый: {price.cost}
                  <span>{price.price_type}</span>
                </p>
              )}
              {price.cost_color && (
                <p className="product__info-price">
                  Цветной: {price.pref && <span>{price.pref} </span>}
                  {price.cost_color}
                  <span>{price.price_type}</span>
                </p>
              )}
              <div className="button-box ">
                <a className="button" href={`tel:${PHONE_NUMBER_MAIN}`}>
                  Заказать
                </a>
              </div>
            </div>
          </div>
          <div className="product__additional-info">
            <MDXRenderer>{body}</MDXRenderer>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export default ProductPage

export const query = graphql`
  query ProductMDX($slug: String) {
    allMdx(filter: { slug: { eq: $slug } }) {
      nodes {
        body
        slug
        id
        frontmatter {
          title
          size
          url
          category {
            name
            alt
          }
          description
          sub {
            name
            alt
          }
          price {
            price_type
            pref
            cost_color
            cost
          }
          weight {
            value
            value_type
          }
          mark
          howMany
          galery {
            childImageSharp {
              gatsbyImageData(
                width: 600
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
            id
          }
          image {
            childImageSharp {
              gatsbyImageData(
                width: 600
                placeholder: BLURRED
                formats: [AUTO, AVIF, WEBP]
              )
            }
            id
          }
          keywords
          type
        }
      }
    }
    mdx {
      body
    }
  }
`
